@import url(https://fonts.googleapis.com/css?family=Share+Tech+Mono);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "Digital";
  src: url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.eot");
  src: url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.eot?#iefix") format("embedded-opentype"), url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.woff2") format("woff2"), url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.woff") format("woff"), url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.ttf") format("truetype"), url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.svg#Digital-7") format("svg");
}
body {
 display: flex;
  justify-content:center;
  background-color: silver;
}
#container {
  background-color: black;
  height: 370px;
  width: 270px;
  border-radius: 20px;
  margin-top: 70px;
  padding-top:20px;
  box-shadow: 6px 5px 19px -4px;
  -webkit-box-shadow: 6px 5px 19px -4px;
  -moz-box-shadow: 6px 5px 19px -4px ;
}

#display {
  background-color:blue;
  color:white;
  height: 80px;
  width: 240px;
  margin: 20px 10px 30px 15px;
  
}
.display-up {
  width:240px;
  height:40px;
  font-family: digital;
  font-size:20px;
  text-align:right;
  padding-top:8px;
} 
.display-down { 
  width:240px;
  height:40px;
 font-family: digital;
  font-size:30px;
  padding-left:10px;
}
  
.btn{
  margin-left:30px;
  
}
button {
  background-color:#1c1a1a;
  color:white;
  margin-bottom:20px;
  margin-right: 2px;
  width: 50px;
  border: 1px solid #141212;
  border-radius: 5px;
    
}
